<template>
  <div class="homeBanner" @click="transRun">
    <div class="banner">
      <img :src="banner" alt="" />
      <div class="pan_ring">
        <div class="pan_zi">
          <img
            :style="`transform: rotate(-${num}deg);transition:${all} 3s`"
            class="index7 img7 ring1"
            id="ring"
            ref="ring"
            src="../../images/img_index7.png"
            alt=""
          />
          <img
            :style="`transform: rotate(${num}deg);transition:${all} 3s`"
            class="index6 img6 ring2"
            id="ring2"
            ref="ring2"
            src="../../images/img_index6.png"
            alt=""
          />
          <img
            :style="`transform: rotate(-${num}deg);transition:${all} 3s`"
            class="index5 img5 ring3"
            id="ring3"
            ref="ring3"
            src="../../images/img_index5.png"
            alt=""
          />
          <img
            :style="`transform: rotate(${num}deg);transition:${all} 3s`"
            class="index4 img4 ring4"
            id="ring4"
            ref="ring4"
            src="../../images/img_index4.png"
            alt=""
          />
          <div class="star">
            <div
              class="star1"
              v-for="(item, i) in imgList"
              :key="i"
              id="star1"
              :class="{ addSatr1: showStar }"
              :style="`display:${
                index === i ? 'block' : 'none'
              }; transition:${all} 0.5s`"
            >
              <img :src="item.img" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import img1 from "../../images/img_index3.png"
import img2 from "../../images/img_index2.png"
import img3 from "../../images/img_index0.png"
import img4 from "../../images/img_index1.png"
export default {
  name: "homeBanner",
  props: ["banner"],
  data () {
    return {
      all: "all",
      index: 0,
      num: 0,
      showStar: false,
      imgList: [
        { id: 0, img: img1 },
        { id: 1, img: img2 },
        { id: 2, img: img3 },
        { id: 3, img: img4 },
        { id: 4, img: img1 },
      ],
      timer: null,
      timer1: null,
      timer2: null,
      totalAngle: 360,
      isRun: false,
      angle: 45
    }
  },
  mounted () {
    this.autoRun()
  },
  methods: {
    autoRun () {
      setTimeout(() => {
        this.transRun()

        this.timer = setInterval(() => {
          this.transRun()
        }, 4000)
      }, 100)
    },
    transRun () {
      if (!this.isRun) {
        setTimeout(() => {
          this.showStar = true
        }, 2800)
        this.isRun = true
        return this.num = this.totalAngle
      }
      if (this.index == 4) {
        clearInterval(this.timer)
        this.all = 'display'
        this.index = 0
        this.num = 0
        setTimeout(() => {
          this.all = 'all'
          this.transRun()
          this.timer = setInterval(() => {
            this.transRun()
          }, 4000)
        }, 100)
      } else if (this.index == 3) {
        this.num = this.num + Number(this.totalAngle + (this.angle * 3))
        this.showStar = false
        setTimeout(() => {
          this.index++
          this.showStar = true
        }, 2800)
      }
      else {
        this.num = this.num + Number(this.totalAngle - this.angle)
        this.showStar = false
        setTimeout(() => {
          this.index++
          this.showStar = true
        }, 2800)
      }
    }
  }


};
</script>

<style lang="less" scoped>
@import "./homeBanner.less";
</style>